<template lang="pug">
  .copy(ref="copyButton")
    .copy__content
      slot
    .copy__value(v-if="title") {{ title }}
</template>

<script>
import Clipboard from 'clipboard';

export default {
  props: {
    value: {
      type: String,
      default: '',
    },

    title: {
      type: String,
      default: '',
    },
  },

  methods: {
    /**
     * Habilita la opcin de copiar el valor
     *
     * TODO: Mostrar un mensaje indicando el resultado de la accin
     */
    initializeClipboard() {
      const clipboard = new Clipboard(this.$refs.copyButton, {
        text: () => this.value,
      });

      // Evento cuando el cdigo se ha copiado correctamente
      clipboard.on('success', e => {
        console.info('Copiado al portapapeles');

        e.clearSelection();
      });

      // Evento cuando el cdigo no se ha copiado y ha ocurrido un error
      clipboard.on('error', () => {
        console.error('Error al copiar el cdigo');
      });
    },
  },

  mounted() {
    this.initializeClipboard();
  },
};
</script>

<style src="./Copy.scss" lang="scss" scoped></style>
